.widget {
  display: flex;
  flex: 1;
  padding: 10px;
  justify-content: space-between;
  box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
  border-radius: 5px;
  height: 130px;

  .left,
  .right {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .title {
      font-weight: bold;
      font-style: 14px;
      color: lightgray;
    }

    .counter {
      font-size: 28px;
      font-weight: 300;
    }

    .link {
      width: max-content;
      font-size: 12px;
      border-bottom: 1px solid gray;
    }

    .percentage {
      display: flex;
      align-items: center;
      font-size: 14px;

      &.positive {
        color: rgb(0, 174, 0);
      }
      &.negative {
        color: red;
      }
    }

    .icon {
      font-size: 2rem;
      padding: 5px;
      background-color: rgba(128, 0, 128, 0.541);
      border-radius: 5px;
      align-self: flex-end;
    }
  }
}
